// components/TrendingContent.tsx
import { getTopAnime, Anime } from '@/lib/api'
import { AnimeGrid } from './anime-grid'

export async function TrendingContent() {
  const data = await getTopAnime(1)
  const animes: Anime[] = data.data ?? []

  if (animes.length === 0) {
    return <p className="text-center text-muted-foreground">No trending anime found</p>
  }

  return <AnimeGrid animes={animes} title="Trending This Week" />
}
